<template>
  <v-card>
    <v-card-text class="nopadding">
      <v-row justify="space-between">
        <v-col cols="12" sm="4">
          <v-card
            style="min-height: 220px;"
            outlined
          >
            <v-card-title class="white--text headline pb-1" style="padding-left: 6px; border-bottom:1px solid #eee;height: 36px;">
              <Add-Department-Btn
                class="mr-2"
                :parentUniqueCode="active[0]"
                @addOrg="getAddDeptMsgFromSon"
              />
              <Edit-Department-Btn
                class="mr-2"
                :parentDeptObj="active[0]"
                :departments="departments"
                @editOrg="getEditDeptMsgFromSon"
              />
              <Remove-Department-Btn
                class="mr-2"
                :parentUniqueCode="active[0]"
                @delOrg="getDelDeptMsgFromSon"
              />
            </v-card-title>

            <v-treeview
              :active.sync="active"
              :items.sync="departments"
              :open.sync="open"
              activatable
              color="warning"
              return-object
              hoverable
              transition
              selection-type="independent"
              @update:active="loadUsers"
              dense
              v-model="selections"
            >
              <template v-slot:prepend="{ open }">
                <v-icon
                  :color="open ? 'green' : 'purple'"
                >{{open ? 'mdi-file-tree-outline' : 'mdi-file-tree'}}</v-icon>
              </template>
            </v-treeview>
          </v-card>
        </v-col>

        <v-divider vertical></v-divider>

        <v-col class="d-flex text-center">
          <v-scroll-y-transition mode="out-in">
            <select-user 
              :departments="departments"
              ref="SelectUser" 
              @selectUserReady="selectUserReady"
            />
          </v-scroll-y-transition>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
// import store from '@/store'
const pause = ms => new Promise(resolve => setTimeout(resolve, ms));

export default {
  mounted() {
    this.$http.get("/user/api/v1/department/selectDeparmentTree").then(res => {
      this.departments = res.data;
      
      if(this.departments.length > 0){
        if(this.isSelectUserReady) {
          // 默认激活第一个节点
          this.active.push(this.departments[0]);
        }

        // 展开树的第一级
        let _this = this;
        this.departments.forEach(function(
          selection,
          index
        ) {
          _this.open.push(selection);
        });
      }
    });
  },

  data: () => ({
    code: "",
    active: [],
    oldActiveObj: {},
    avatar: null,
    open: [],
    selectable: true,
    activatable: true,
    departments: [],
    pageNum: 1,
    pageSize: 5,
    selections: [],
    isSelectUserReady: false,
  }),

  created(){
    
  },

  computed: {},

  watch: {
    
  },

  methods: {
    selectUserReady() {
      this.isSelectUserReady = true;
      if(this.departments.length > 0){
        // 默认激活第一个节点
        this.active.push(this.departments[0]);
      }
    },

    loadUsers(array) {
      // 防止再次点击活跃节点时取消，必须有一个节点处于活跃，未找到相应设置，暂用代码方式实现
      if(array.length == 0) {
        if(!!this.oldActiveObj.id) {
          this.active.push(this.oldActiveObj);
        }

        return;
      }

      // 当再次点击同一节点时，不再刷新用户列表
      if(!!this.oldActiveObj.id && this.oldActiveObj.id == array[0].id) {
        return;
      }

      this.oldActiveObj = Object.assign({}, array[0]);

      this.$refs.SelectUser.deptArray = array;
      this.$refs.SelectUser.loadUsers();
    },

    getAddDeptMsgFromSon(
      departmentName,
      departmentCode,
      numIndex,
      description,
      parentUniqueCode
    ) {
      let postData = {
        departmentName: departmentName,
        parentUniqueCode: parentUniqueCode,
        departmentCode: departmentCode,
        numIndex: numIndex,
        description: description
      };
      this.$http
        .post("/user/api/v1/department/addDepartment", postData)
        .then(response => {
          if (response.success) {
            response.msg = "部门添加成功";
            this.active[0].children.push(response.data);
          }
          this.$store.commit("SET_SNACK", response);
        })
        .catch(error => {
          console.log(error);
        });
    },

    getDelDeptMsgFromSon(unicode) {
      this.$http
        .get("/user/api/v1/department/selectDeparmentTree")
        .then(res => {
          let paramLenth = this.departments.length;
          this.departments.splice(0, paramLenth, ...res.data);
          this.active = [];
        });
    },

    getEditDeptMsgFromSon() {
      this.$http
        .get("/user/api/v1/department/selectDeparmentTree")
        .then(res => {
          let paramLenth = this.departments.length;
          this.departments.splice(0, paramLenth, ...res.data);
        });
    }
  },

  components: {
    AddDepartmentBtn: () => import("./component/organizations/AddDepartment"),
    SelectUser: () => import("./component/users/user"),
    RemoveDepartmentBtn: () => import("./component/organizations/RemoveDepartment"),
    EditDepartmentBtn: () => import("./component/organizations/EditDepartment")
  }
};
</script>



